import Button from '../reusable/Button';
import FormInput from '../reusable/FormInput';
import {useCallback, useRef, useState} from "react";
import emailjs from '@emailjs/browser';
import {ContactMeProvider} from "../../context/ContactMeContext";

const ContactForm = () => {
	const [send, setSend] = useState("Not yet sent");
	const [error, setError] = useState("");
	const form = useRef();

	const sendEmail = useCallback((e) => {
		e.preventDefault();
		setSend("sending");
		emailjs.sendForm('service_wg3op97', 'template_rrg69cx', form.current, 'ffBCYbjlYJo_DXMRT')
			.then((result) => {
				console.log("success",result.text);
				setSend("success");
			}, (error) => {
				console.log("error",error.text);
				setSend("error");
				setError(error.text);
			});
	},[]);

	let content = <p></p>
	if( send === "sending") { content = <p>Sending...</p> }
	if( send === "error") { content = <p>OPS please wait a second and try again</p> }
	if( send === "success") { content = <p>Thank you for your message!</p> }

	return (
		<ContactMeProvider>
		<div className="w-full lg:w-1/2">
			<div className="leading-loose">
				<form
					ref={form}
					onSubmit={sendEmail}
					className="max-w-xl m-4 p-6 sm:p-10 bg-secondary-light dark:bg-secondary-dark rounded-xl shadow-xl text-left"
				>
					<p className="font-general-medium text-primary-dark dark:text-primary-light text-2xl mb-8">
						Contact Form
					</p>
					<FormInput
						inputLabel="Full Name"
						labelFor="name"
						inputType="text"
						inputId="from_name"
						inputName="from_name"
						placeholderText="Your Name"
						ariaLabelName="Name"
					/>
					<FormInput
						inputLabel="Email"
						labelFor="email"
						inputType="email"
						inputId="from_visitor_email"
						inputName="from_visitor_email"
						placeholderText="Your email"
						ariaLabelName="Email"
					/>
					<FormInput
						inputLabel="Subject"
						labelFor="subject"
						inputType="text"
						inputId="subject"
						inputName="subject"
						placeholderText="Subject"
						ariaLabelName="Subject"
					/>

					<div className="mt-6">
						<label
							className="block text-lg text-primary-dark dark:text-primary-light mb-2"
							htmlFor="message"
						>
							Message
						</label>
						<textarea
							className="w-full px-5 py-2 border border-gray-300 dark:border-primary-dark border-opacity-50 text-primary-dark dark:text-secondary-light bg-ternary-light dark:bg-ternary-dark rounded-md shadow-sm text-md"
							id="message"
							name="message"
							cols="14"
							rows="6"
							aria-label="Message"
						></textarea>
					</div>

					<div className="font-general-medium w-40 px-4 py-2.5 text-white text-center font-medium tracking-wider bg-indigo-500 hover:bg-indigo-600 focus:ring-1 focus:ring-indigo-900 rounded-lg mt-6 duration-500">
						<Button
							title="Send Message"
							type="submit"
							aria-label="Send Message"
						/>
					</div>
					<p>{content}</p>
				</form>
			</div>
		</div>
		</ContactMeProvider>
	);
};

export default ContactForm;
